}

/*#region ############ START: toastr Styles  */

#toast-container > div {
  opacity: 1;
  transition: all 0.3s ease-out;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}

.toast-container .ngx-toastr {
  padding: 10px 5px 10px 45px;
}

.toast-container .ngx-toastr .toast-title {
  margin-bottom: 5px;
}

.toast-bottom-center {
  bottom: 10px;
  margin: 0 auto;
  right: 0;
  left: 0;
  width: 100%;
}

.toast-top-center {
  bottom: 0;
  margin: 0 auto;
  right: 0;
  left: 0;
  top: 10px;
  width: 100%;
}

.toast-error {
  background-color: #ffe7e6;
  border: solid #ff5757;
  border-width: 0 0 0 6px;
  color: #ff5757 !important;
}

.toast-success {
  background-color: #bce3d3;
  border: solid #1ea97c;
  border-width: 0 0 0 6px;
  color: #1ea97c !important;
  /* background-image: url("./assets/img/ico/favicon-32.png") !important; */
}

.toast-info {
  background-color: #e9e9ff;
  border: solid #696cff;
  border-width: 0 0 0 6px;
  color: #696cff !important;
}

.toast-warning {
  background-color: #fff2e2;
  border: solid #cc8925;
  border-width: 0 0 0 6px;
  color: #cc8925 !important;
}

.toast-container .ngx-toastr .toast-close-button {
  padding: 0px 12px !important;
}

/*#endregion ############ END: Toast Styles */

