/*#region  ############ START:  material table ################ */

.table-container {
  overflow-x: auto;
  overflow-y: scroll !important;
  width: 100%;

  tr.mat-header-row {
    height: 47px !important;
  }

  th.mat-header-cell {
    color: #374649;
    //  text-transform: uppercase;
    font-size: 15px;
    font-weight: 500;
    padding: 15px 12px 14px 45px !important;
    border-top: 1px solid #e0e0e0;
    border-bottom: 1px solid #eaeaea;
    position: relative;
  }

  th.mat-header-cell:not(:last-child) {
    &::before {
      content: "";
      position: absolute;
      height: 86%;
      width: 100%;
      top: 6%;
      right: 0;
      border-right: 1px solid #eaeaea;
    }
  }

  td.mat-cell {
    color: #101317;
    font-size: 1rem;
    padding: 27px 6px 28px 45px !important;
  }

  td.mat-cell:last-of-type {
    padding: 10px !important;
    text-align: center;
    min-width: 77px;
  }

  .mat-checkbox-inner-container {
    margin: 0 !important;
  }
}

.table-in-forms .table-container {
  overflow-y: auto !important;
  border: 1px solid #e0e0e0;
  border-radius: 9.6px !important;

  th.mat-header-cell {
    border-top: none;
    border-bottom: 1.5px solid #eaeaea;
    padding: 15px 12px 14px 20px !important;
  }

  td.mat-cell {
    border-bottom: none;
    padding: 15px 6px 15px 20px !important;
    // padding-top: 15px !important;
    // padding-bottom: 15px !important;
  }

  th.mat-header-cell:not(:last-child) {
    &::before {
      content: none;
    }
  }
}

.small-table .table-container {
  width: 60%;
  margin: 0 15px;
}

