/* START: Employee Work Schedule calendar ##################################### */
.calendar-header {
  text-align: center;
  font-size: 16px;
}

.calendar-header > p {
  text-decoration: underline;
  color: #30b79d;
}

.calendar-header span.workdays {
  font-size: 12px !important;
}

.calendar-body {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  background: #efefef;
  border: 1px solid #ddd;
  padding: 5px;
  border-radius: 4px;
}

.calendar-body .popover-body:empty {
  padding: 0 !important;
}

.square {
  padding: 10px;
  width: 60px;
  height: 60px;
  display: flex;
  margin: 3px;
  justify-content: center;
  align-items: center;
  position: relative;
  /*transition: all 0.2s linear;*/
  overflow: hidden;
}

.square:hover {
  background-color: #fff;
  color: #30b79d;
  border-radius: 50%;
  cursor: pointer;
